import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import PropTypes from 'prop-types'
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';


import '../../styles/navbar.css'

const useStyles = makeStyles((theme) => ({
    '@global': {
        ul: {
            margin: 0,
            padding: 0,
            listStyle: 'none',
        },
    },
    appBar: {
        borderBottom: `1px solid ${theme.palette.divider}`,
    },
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    toolbar: {
        borderBottom: `1px solid ${theme.palette.divider}`,
      },
      toolbarTitle: {
        flex: 1,
      },
      toolbarSecondary: {
        justifyContent: 'space-between',
        overflowX: 'auto',
      },
}));

export default function Navbar({ name }) {
    const classes = useStyles();

    const [types, setTypes] = useState([])

    useEffect(() => {
        axios({
            method: "GET",
            url: 'http://127.0.0.1:8000/api/type/'
        }).then(response => {
            setTypes(response.data)
        })
    }, [])

    const logout = async () => {
        await fetch('http://127.0.0.1:8000/api/logout', {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            credentials: 'include',
        });
    }

    let menu;
    let header;

    if (name === undefined) {
        header = (
            <Toolbar className={classes.toolbar}>
                <Typography
                component="h2"
                variant="h5"
                color="inherit"
                align="center"
                noWrap
                className={classes.toolbarTitle}
                >
                <Link className="nav" to='/'>WorkMaster</Link>
                </Typography>
                <Button variant="outlined" size="small">
                <Link className="nav" to='/login'>Войти</Link>
                </Button>
                <Button variant="outlined" size="small">
                <Link className="nav" to='/register'>Зарегистрироваться</Link>
                </Button>
            </Toolbar>
        )
        menu = null
    } else {
        header = (
            <Toolbar className={classes.toolbar}>
                <Typography
                component="h2"
                variant="h5"
                color="inherit"
                align="center"
                noWrap
                className={classes.toolbarTitle}
                >
                <Link className="nav" to='/'>WorkMaster</Link>
                </Typography>
                <Button variant="outlined" size="small">
                <Link to='/login' onClick={logout}>Выйти</Link>
                </Button>
            </Toolbar>
        )
        menu = (
            <Toolbar component="nav" color='primary' variant="dense" className={classes.toolbarSecondary}>
                {types.map(c => (
                                <Link className="nav" to={{ pathname: `/type/${c.id}/`, fromDashboard: false }} key={c.id}>{c.name}</Link>
                            ))}
                                <Link className="nav" to='/myposts'>Мои посты</Link>
                                <Link className="nav" to='/newpost'>Создать пост</Link>
            </Toolbar>
        )
    }

    return (
        <AppBar color="default" elevation={0} className={classes.appBar}>
            {header}
            {menu}
        </AppBar>
    );
}
Navbar.propTypes = {
    name: PropTypes.string
}